














































/* eslint-disable @typescript-eslint/no-empty-interface */
/* eslint-disable @typescript-eslint/ban-ts-comment */
import Vue from 'vue'
import { install } from '@github/hotkey'
import { KBarAction } from '@/interfaces'

type Data = {
  show: boolean
  search: string
  actionItems: KBarAction[]
}

type Props = {
  actions: KBarAction[]
}

type Computed = {

}

type Methods = {
  onSearch: (search: string) => void,
  onToggle: () => void
  closeKbar: () => void
  handlePerformAction: (action: KBarAction) => void
}

export default Vue.extend<Data, Methods, Computed, Props>({
  name: 'VKbarProvider',
  props: {
    actions: {}
  },
  data: () => ({
    show: false,
    search: '',
    actionItems: []
  }),
  mounted () {
    // @ts-ignore
    const el = this.$el
    // @ts-ignore
    install(el, 'Control+k,Meta+k')
    el.addEventListener('click', () => this.onToggle())
  },
  watch: {
    show: {
      immediate: true,
      handler (state) {
        if (!state) {
          this.search = ''
        }
      }
    },
    search: {
      immediate: true,
      handler: 'onSearch'
    }
  },
  methods: {
    closeKbar () {
      this.show = false
    },
    onToggle () {
      this.show = !this.show
    },
    onSearch (search: string) {
      if (!search) {
        this.actionItems = this.actions
        return
      }

      this.actionItems = this.actions.filter(item => {
        if (item.name.toLowerCase().includes(search)) {
          return item
        } else if (item.description?.toLowerCase().includes(search)) {
          return item
        } else if (item.keywords?.includes(search)) {
          return item
        }
      })
    },
    handlePerformAction (action: KBarAction) {
      this.closeKbar()
      action.perform()
    }
  }
})
